<template>
  <!-- 


      Í≥µÎ∞±, ÌäπÏàòÎ¨∏Ïûê ÏïàÎì§Ïñ¥Í∞ÄÍ≤å ÎßâÍ∏∞






      


  -->
    <div>
        <h1 class="main-title">Î∂ÅÎßàÌÅ¨ ÌéòÏù¥ÏßÄÏûÖÎãàÎã§</h1>


        <!-- Î∂ÅÎßàÌÅ¨ ÏÉùÏÑ±, ÏàòÏ†ï Î™®Îã¨ ÏãúÏûë -->
        <div>
          <v-row justify="center">
              <v-dialog v-model="dialog" scrollable max-width="350px">
                <template v-slot:activator="{ on, attrs }">
                  <v-btn v-if="editMode == false" color="#2ac187" dark v-bind="attrs" v-on="on">Ïª¨Î†âÏÖò ÎßåÎì§Í∏∞</v-btn>
                  <v-btn v-else disabled color="#2ac187" dark v-bind="attrs" v-on="on">Ïª¨Î†âÏÖò ÎßåÎì§Í∏∞</v-btn>
                  <div>
                    <v-btn v-if="editMode == true" @click="manager()">Î∂ÅÎßàÌÅ¨Í¥ÄÎ¶¨ OFF</v-btn>
                    <v-btn v-else @click="manager()">Î∂ÅÎßàÌÅ¨Í¥ÄÎ¶¨ ON</v-btn>
                  </div>
                </template>
              <v-card class="mx-auto">
                <v-card-title>
                  <span v-if="editMode == true" class="mx-auto text-h8">Ïª¨Î†âÏÖò ÏàòÏ†ï</span>
                  <span v-else class="mx-auto text-h8">Ïª¨Î†âÏÖò ÎßåÎì§Í∏∞</span>
                </v-card-title>
                    <v-card-text>
                          <div>

                            <!-- ÏàòÏ†ï dialog -->
                            <template v-if="editMode == true">
                              <v-text-field
                                v-model="editedCatgName"
                                @change ="editedCatgName"
                                label="*ÏàòÏ†ïÌï† Ïù¥Î¶ÑÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî!"
                                :rules="rules"
                                hide-details="auto" />
                            </template>

                            <!-- ÏÉùÏÑ± dialog -->
                            <template v-else>
                              <v-text-field
                                width="300"
                                v-model="newCatgName"
                                label="*Ïª¨Î†âÏÖòÏù¥Î¶ÑÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî!"
                                :rules="rules"
                                hide-details="auto" />
                              <!-- <v-text-field  v-model="catgName" label="*Ïª¨Î†âÏÖòÏù¥Î¶ÑÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî!" required /> -->
                            </template>

                          </div>
                      </v-card-text>

                      <!-- ÏàòÏ†ïÎ™®Îìú dialog -->
                      <v-card-actions v-if="editMode == true">
                        <v-spacer></v-spacer>
                          <v-btn  v-if="editedCatgName.length >=1 && editedCatgName.length<=10 && catgNames.indexOf(editedCatgName) == -1 && checkSpace(editedCatgName) == false && checkSpecial(editedCatgName) == false" 
                                  color="blue darken-1" text 
                                  @click="editCollection(catgId, memberId)">Ï†ÄÏû•</v-btn>

                          <v-btn color="blue darken-1" text @click="cancel()">Ï∑®ÏÜå</v-btn>
                        </v-card-actions>

                        <!-- ÏÉùÏÑ±Î™®Îìú dialog -->
                        <v-card-actions v-else>
                          <v-spacer></v-spacer>
                          <v-btn  v-if="newCatgName.length >=1 && newCatgName.length<=10 && catgNames.indexOf(newCatgName) == -1 && checkSpace(newCatgName) == false && checkSpecial(newCatgName) == false" 
                                  color="blue darken-1" text 
                                  @click="createCollection(memberId)">Ï†ÄÏû•</v-btn>

                          <v-btn color="blue darken-1" text @click="cancel()">Ï∑®ÏÜå</v-btn>
                        </v-card-actions>

                    </v-card>
                </v-dialog>
            </v-row>
        </div>
        <!-- Î™®Îã¨ ÎÅù -->


        <!-- Ïª¨Î†âÏÖò ÏãúÏûë -->
          <div  v-for="(collection,i) in collections" :key="i"
                class="container"
                style="cursor:pointer;">
              <div class="content">
                  <div class="content-overlay"></div>
                
                  
                  <!-- ÏóÜÏúºÎ©¥ ÎîîÌè¥Ìä∏ Ïù¥ÎØ∏ÏßÄ -->
                  <v-img  v-if ='collection.postId == ""'
                          class="white--text align-end rounded-sm dark" 
                          aspect-ratio="1.2" 
                          @click="goBookmark(collection.catgId, collection.catgName)"
                        :src="require('@/assets/image/sns/default/bookmark_default.png')" />

                  <!-- Ï†ÄÏû• Ìè¨Ïä§Ìä∏Ïùò Ï≤´Î≤àÏß∏ Ïç∏ÎÑ§Ïùº Ïª§Î≤ÑÏÇ¨ÏßÑÏúºÎ°ú  -->
                  <v-img v-else class="white--text align-end rounded-sm dark" 
                          aspect-ratio="1.2"
                          @click="goBookmark(collection.catgId, collection.catgName)"
                          :src="require(`@/assets/image/sns/${collection.postId}/${collection.thumbnail}`)" />

                  <!-- Í¥ÄÎ¶¨Î≤ÑÌäº - ÏàòÏ†ï, ÏÇ≠Ï†ú -->
                  <div  v-if="management == true" class="content-details fadeIn-bottom">
                    <v-btn  v-if='collection.catgName!= "default" && editMode == true'
                            class="text--white" 
                            color="#2ac187" 
                            dark v-bind="attrs"
                            v-on="on" 
                            @click="showEditForm(collection.catgId)">ÏàòÏ†ï</v-btn>

                    <v-btn  v-if='collection.catgName!= "default" && editMode == true' 
                            class="text--white" 
                            color="#2ac187" 
                            @click="swal(collection.memberId,collection.catgId)">ÏÇ≠Ï†ú</v-btn>

                    <!-- <h3 class="content-title">{{collection.catgName}}</h3> -->
                  </div>

                  <div v-else class="content-details">
                    <h3 class="content-title">{{collection.catgName}}</h3>
                  </div>
                  <!-- <p class="content-text">This is a short description</p> -->
              </div>
          </div>
        <!-- Ïª¨Î†âÏÖò ÎÅù -->
    </div>
</template>
<script>
  export default {
      name: "Collection",
      data() {
          return {
              collections:[],
              dialog: false,
              newCatgName: "",
              catgId:"",
              memberId : this.$store.state.id,
              tnPostId:Number,
              thumbnail:"",
              dummy:"",//Ìï¥Îãπ Ïú†Ï†ÄÏóêÍ≤å ÏÉùÏÑ±Îêú Ïª¨Î†âÏÖòÏù¥ ÏóÜÏùÑ Ïãú ÏûêÎèôÏÉùÏÑ±ÎêòÎèÑÎ°ù ÎèÑÏôÄÏ£ºÎäî ÎçîÎØ∏
              management: false, //Î∂ÅÎßàÌÅ¨Í¥ÄÎ¶¨
              editMode:false, //ÏàòÏ†ïÎ™®Îìú
              editedCatgName: "", //ÏàòÏ†ïÌï† Ïª¨Î†âÏÖò Ïù¥Î¶Ñ

              catgNames: [], //Î≥∏Ïù∏ Ïª¨Î†âÏÖò Ïù¥Î¶ÑÎì§

              //Ïª¨Î†âÏÖò Ïù¥Î¶Ñ rules
              rules: [
                value => !!value || 'Ïù¥Î¶ÑÏùÄ ÏûÖÎ†• Î∂ÄÌÉÅÎìúÎ¶ΩÎãàÎã§üôè', //Ïù¥Î¶Ñ ÏóÜÏúºÎ©¥
                value => (value && value.length <= 10) || '10Í∏ÄÏûê Ïù¥ÎÇ¥Î°ú Î∂ÄÌÉÅÎìúÎ¶ΩÎãàÎã§üôè', //10Í∏ÄÏûê Ïù¥ÎÇ¥Ïù¥Î©¥
                value => (this.catgNames.indexOf(value) == -1) || 'Ïù¥ÎØ∏ Ï°¥Ïû¨ÌïòÎäî Ïù¥Î¶ÑÏûÖÎãàÎã§üôè', //Ïù¥ÎØ∏ Ï°¥Ïû¨ÌïòÎäî Ïù¥Î¶ÑÏù¥Î©¥
                value => (this.checkSpace(value) == false) || 'Í≥µÎ∞±ÏùÄ ÏûêÏ†ú Î∂ÄÌÉÅÎìúÎ¶ΩÎãàÎã§üôè', //Í≥µÎ∞±Ïù¥ ÏûàÏúºÎ©¥
                value => (this.checkSpecial(value) == false) || 'ÌäπÏàòÎ¨∏ÏûêÎäî ÏÇ¨Ïö© ÏûêÏ†ú Î∂ÄÌÉÅÎìúÎ¶ΩÎãàÎã§üôè', //ÌäπÏàòÎ¨∏ÏûêÍ∞Ä ÏûàÏúºÎ©¥
              ],
          }
      },
      setup() {
          
      },
      created() {
          this.getCollectionList(this.memberId);
          this.management = false;
          this.editMode = false;
          this.dialog = false;
      },
      mounted() {
      
      },
      unmounted() {
      
      },
      watch: {
        // inputE(val) { //ÏûÖÎ†•Ìïú Í∞í Î∞õÏïÑÏôÄÏÑú
        //   if (!val) { //ÏûÖÎ†•Ìïú Í∞íÏù¥ ÏóÜÏúºÎ©¥ return
        //       return
        //   }
        //   this.createCollection() //ÏûàÏúºÎ©¥
        // },
      },
      methods: {

        // Í≥µÎ∞±Ïù¥ ÏûàÎÇò ÏóÜÎÇò Ï≤¥ÌÅ¨
        checkSpace(str) {
          if(str.search(/\s/) !== -1) {
            return true; //Í≥µÎ∞±O
          } else {
            return false; //Í≥µÎ∞±X
          }
        },

        // ÌäπÏàò Î¨∏ÏûêÍ∞Ä ÏûàÎÇò ÏóÜÎÇò Ï≤¥ÌÅ¨
        checkSpecial(str) {
          let bannedPattern = /[!?@#$%^&*():;+-=~{}<>\_\[\]\|\\\"\'\,\.\/\`\‚Ç©]/gi;

          if(bannedPattern.test(str)) {
            return true; //Í∏àÏßÄÎ¨∏ÏûêO
          } else {
            return false; //Í∏àÏßÄÎ¨∏ÏûêX
          }
        },


        //Ïª¨Î†âÏÖò Í¥ÄÎ¶¨
        manager(){
          this.management = !this.management;
          this.editMode = !this.editMode;
          console.log("managemenet: "+ this.management);
          console.log("editMode: "+ this.editMode);
        },


        //dialog ÏàòÏ†ï
        cancel(){
          this.dialog = false;
          this.editedCatgName = "";
        },


        //Ïª¨Î†âÏÖòÎ¶¨Ïä§Ìä∏
        getCollectionList(memberId){

            this.axios('/sns/collection/' + memberId, {
                params: {
                    memberId: memberId,
                }
            }).then(res => {
                
                if(res.data.length == 0){ // ÎßåÎì§Ïñ¥ÎÜìÏùÄ Ïª¨Î†âÏÖòÏù¥ Ï°¥Ïû¨ÌïòÏßÄ ÏïäÏúºÎ©¥ ÎîîÌè¥Ìä∏ Ïª¨Î†âÏÖò ÏÉùÏÑ±
                  this.createDefaultCollection(memberId);

                }else{ // ÎßåÎì† Ïª¨Î†âÏÖòÏù¥ Ï°¥Ïû¨ÌïòÎ©¥ Ïù¥Î¶ÑÎßå ÎΩëÏïÑÏÑú Î∞îÏù∏Îî©

                  for(let i=0; i<res.data.length; i++){
                    let isCatgName = res.data[i].catgName;
                    this.catgNames.push(isCatgName);

                  }

                    this.collections = res.data;

                }

            }).catch(err => {
                alert(err);
            });
        },


        //ÎîîÌè¥Ìä∏ Ïª¨Î†âÏÖò ÏÉùÏÑ± - Ìï¥Îãπ Ïú†Ï†Ä ÏïÑÏù¥ÎîîÏóê ÏÉùÏÑ±Îêú Ïª¨Î†âÏÖòÏù¥ ÏóÜÏúºÎ©¥ Í∏∞Î≥∏Ïª¨Î†âÏÖò ÏÉùÏÑ±Îê®
        createDefaultCollection(memberId){

          const thumbnail = 'bookmark_default.png'

          this.axios.post('/sns/collection', {
              memberId : memberId,
              catgName : 'default',
              thumbnail : thumbnail,
          }).then(res => {
              this.getCollectionList(memberId);
          }).catch(err => {
              alert(err);
          });

        },


        //Ïª¨Î†âÏÖòÏÉùÏÑ±
        createCollection(memberId){     

            const thumbnail = 'bookmark_default.png'
            this.dialog = false;

            this.axios.post('/sns/collection', {
                memberId : memberId,
                catgName : this.newCatgName,
                thumbnail : thumbnail,

            }).then(res => { 

                this.dialog = false;
                this.newCatgName = ""
                this.getCollectionList(memberId);

            }).catch(err => {
                alert(err);
            });

        },

        
        // ÏàòÏ†ïÌèº Ìò∏Ï∂ú
        showEditForm(catgId) {

          this.dialog =! this.dialog;
          this.catgId = catgId; //ÏàòÏ†ïÌÅ¥Î¶≠Ìïú Ïª¨Î†âÏÖò Id Î∞îÏù∏Îî©

        },


        //Ïª¨Î†âÏÖòÏàòÏ†ï(Ïù¥Î¶Ñ)
        editCollection(catgId, memberId){

          // if (this.editedCatgName == "" || this.editedCatgName == undefined) {
          //   return;
          // }
          // else if(this.catgNames.indexOf(this.editedCatgName) == -1){
          //   return;
          // }
          // else if(this.catgName.length > 11){
          //   return;

          // } else { }
          this.axios

            .put("/sns/collection", {
              catgId:catgId,
              catgName: this.editedCatgName,
            })

            .then((res) => {
              console.log("Ïª¨Î†âÏÖòÏù¥Î¶ÑÏàòÏ†ï ÏÑ±Í≥µ! " + res);
              this.$swal('Ïª¨Î†âÏÖò Ïù¥Î¶ÑÏù¥ ÏàòÏ†ïÎêòÏóàÏäµÎãàÎã§üôè');
              this.dialog = !this.dialog;
              this.editedCatgName = "";
              this.manager();
              // this.editMode = false;
              // this.management = false;
              this.getCollectionList(memberId);
            })
            .catch((err) => {
              console.log(err);
            });
          
        },

        
          //ÏÇ≠Ï†úswal
          swal(memberId,catgId) {

            this.$swal({
              title: "Ï†ïÎßê ÏÇ≠Ï†úÌï†ÍπåÏöî?",
              text: "ÏÇ≠Ï†úÎêú Ïª¨Î†âÏÖòÏùÄ Î≥µÍµ¨Í∞Ä Î∂àÍ∞ÄÎä•Ìï©ÎãàÎã§.üôè",
              icon: "warning",
              showCancelButton: true,
              confirmButtonColor: "#2ac187",
              cancelButtonColor: "#d33",
              cancelButtonText: "Ï∑®ÏÜå",
              confirmButtonText: "ÎÑ§, ÏÇ≠Ï†úÌï†Í≤åÏöî!",
            }).then((result) => {
              if (result.isConfirmed) {
                this.deleteClctn(memberId,catgId);
                this.$swal("ÏÇ≠Ï†ú ÏôÑÎ£å!", "Í≤åÏãúÍ∏ÄÏù¥ ÏÇ≠Ï†úÎêòÏóàÏäµÎãàÎã§.", "success");
              }
            });

          },


          //Ïª¨Î†âÏÖò ÏÇ≠Ï†ú
          deleteClctn(memberId,catgId) {

            this.swal();
            this.axios
              .delete("/sns/collection/" + catgId)
              .then((res) => {
                console.log("Ïª¨Î†âÏÖòÏÇ≠Ï†ú ÏÑ±Í≥µ! " + res);
                this.manager();
                this.getCollectionList(memberId);
              })
              .catch((err) => {
                alert(err);
              });

          },


          //Ìï¥Îãπ Ïª¨Î†âÏÖòÏúºÎ°ú Ïù¥Îèô
          goBookmark(catgId,catgName){
            console.log(catgId);
            console.log(catgName);
            this.$router.push({ path: '/snsBookmarkDetail', query: {catgId : catgId, catgName : catgName} });
          }

       },
    };
</script>

<style scoped lang="css" src="@/assets/css/sns/Collection.css" />
    